// ----------------/footer-modal/-----------

.footer-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // overflow: auto;
    z-index: 1010;
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 500ms cubic-bezier(0.4, 0, 0.2, 1);
    background-color: rgba(0, 0, 0, 0.4);
}


.footer-modal {
    width: 320px;
    padding: 10px;
    border-radius: 5px;
    max-height: calc(100vh - 100px);
    overflow-y: auto;
    background-color: $light-text-color;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 500ms cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 2px 2px rgba(0, 0, 0, 0.2);

    @include tablet {
        width: 700px;
        height: 600px;
        padding: 15px;
    }

    @include desktop {
        width: 700px;
        height: 600px;
        padding: 15px;
    }
}

.footer-closeModalBtn {
    position: absolute;
    padding: 0;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        background-color: $accent-color;
    }
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    pointer-events: none;    
    overflow: hidden;
    display: none;
}

.team-title {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    margin-top: 35px;
    margin-bottom: 20px;
    color:$primary-text-color;

    @include tablet {
        font-size: 22px;
        font-weight: 700;
        margin-top: 5px;
       
    }
}

// -----photo-------------
.team-list {
    display: inline;

    @include tablet {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 30px;
    }
}

.team-item {
    flex-basis: calc(100% / 4);
    text-align: center;
}

.team-card {
    border-radius: 6px;

    @include tablet {
        &:hover {
            box-shadow: 4px 10px 48px 5px rgba(0, 0, 0, 0.5);
            transform: scale(1.2);
            transition: 200ms;
        }
    }
}

.team-image {
    width: 160px;
    height: 160px;
    border-radius: 4px;
    margin: 0 auto;
}

.team-item-title {
    font-size: 14px;
    font-weight: 500;

    @include tablet {
        font-size: 17px;
        font-weight: 700;
    }
}

.github-link {
    display: inline;
}

.icon-github {
    fill: purple;
    border-radius: 50;

    &:hover {
        fill: $accent-color;
    }
}

.scale-down-center {
    -webkit-animation: scale-down-center 1.2s linear infinite both;
    animation: scale-down-center 1.2s linear infinite both;
}

@-webkit-keyframes scale-down-center {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    100% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
}

@keyframes scale-down-center {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    100% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
}

.hidden-photo {
    display: none;
}

.team-list{
    color: $primary-text-color;
}