.loader {
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, .5);
  justify-content: center;
  align-items: center;
  transition: opacity .25s, visibility .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #000;
  border-bottom-color: #ff6b08;
  border-radius: 50%;
  animation: loading .75s infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background: rgba(0, 0, 0, .7);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.2);
}

.modal {
  height: 100vh;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  padding: 48px 20px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

@media screen and (min-width: 480px) {
  .modal {
    width: 470px;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

